:host {
  display: block;
}

.produits-mobile-app {
  display: none;
}

@media screen and (max-width: 768px) {
  :host {
    --mobile-primary: #5f6bff;
    --mobile-text: #334155;
    --mobile-muted: #7f8ca3;
    --mobile-card: #f9fbff;
    --mobile-border: #dbe2ee;
  }
  .produits-desktop {
    display: none;
  }
  .produits-mobile-app {
    display: flex;
    flex-direction: column;
    min-height: 100vh;
    min-height: 100dvh;
    height: 100dvh;
    height: 100vh;
    overflow: hidden;
    margin-top: calc(-0.25rem - env(safe-area-inset-top));
    margin-left: -0.875rem;
    margin-right: -0.875rem;
    margin-bottom: calc(-0.875rem - env(safe-area-inset-bottom));
    padding: 0 0.875rem env(safe-area-inset-bottom);
    padding-left: calc(0.875rem + env(safe-area-inset-left));
    padding-right: calc(0.875rem + env(safe-area-inset-right));
    background: linear-gradient(180deg, #f3f5f9 0%, #eaedf4 58%, #e5eaf1 100%);
    animation: mobileFade 0.24s ease-out;
  }
  .mobile-header {
    flex-shrink: 0;
    display: flex;
    align-items: center;
    gap: 0.55rem;
    margin: 0 -0.875rem 0;
    padding: calc(0.5rem + env(safe-area-inset-top)) calc(0.875rem + env(safe-area-inset-right)) 0.6rem calc(0.875rem + env(safe-area-inset-left));
    background: var(--mobile-card);
    border-bottom: 1px solid var(--mobile-border);
    box-shadow: 0 1px 3px rgba(15, 23, 42, 0.06);
    position: sticky;
    top: 0;
    z-index: 30;
  }
  .mobile-scroll {
    flex: 1;
    min-height: 0;
    overflow-y: auto;
    -webkit-overflow-scrolling: touch;
    padding-top: 0.75rem;
    padding-bottom: calc(4rem + env(safe-area-inset-bottom));
  }
  .mobile-back,
  .mobile-add {
    width: 2.15rem;
    height: 2.15rem;
    border-radius: 999px;
    flex-shrink: 0;
    color: var(--mobile-primary) !important;
    background: rgba(95, 107, 255, 0.1) !important;
  }
  .mobile-title-wrap {
    flex: 1;
    min-width: 0;
  }
  .mobile-title {
    margin: 0;
    font-size: 1.35rem;
    line-height: 1.1;
    font-weight: 800;
    color: var(--mobile-text);
  }
  .mobile-subtitle {
    margin: 0.2rem 0 0;
    font-size: 0.76rem;
    font-weight: 600;
    color: var(--mobile-muted);
  }
  .mobile-search {
    background: #f7f9fd;
    border: 1.5px solid var(--mobile-border);
    border-radius: 0.95rem;
    padding: 0.35rem 0.55rem;
    box-shadow: inset 0 1px 0 rgba(255, 255, 255, 0.7);
  }
  .mobile-search .p-iconfield,
  .mobile-search .p-inputtext {
    width: 100%;
  }
  .mobile-search .p-inputtext {
    border: 0;
    box-shadow: none;
    background: transparent;
    color: var(--mobile-text);
    font-size: 0.96rem;
  }
  .mobile-search .p-inputtext::placeholder {
    color: #92a0b7;
  }
  .mobile-quick-actions {
    display: flex;
    align-items: center;
    gap: 0.4rem;
    padding: 0 0.15rem;
  }
  .mobile-quick-actions p-button {
    flex: 1;
  }
  .mobile-list {
    display: flex;
    flex-direction: column;
    gap: 0.65rem;
    margin-top: 1rem;
  }
  .mobile-produit-card {
    display: flex;
    align-items: flex-start;
    gap: 0.75rem;
    background: var(--mobile-card);
    border: 1px solid var(--mobile-border);
    border-radius: 1rem;
    padding: 0.9rem;
    box-shadow: 0 10px 24px rgba(15, 23, 42, 0.06);
    cursor: pointer;
    transition: box-shadow 0.2s ease;
  }
  .mobile-produit-card:active {
    box-shadow: 0 4px 12px rgba(15, 23, 42, 0.08);
  }
  .mobile-card-media {
    flex-shrink: 0;
  }
  .mobile-card-image-placeholder {
    width: 64px;
    height: 64px;
    border-radius: 0.65rem;
    background: #eef1f6;
    border: 1px solid var(--mobile-border);
    display: flex;
    align-items: center;
    justify-content: center;
  }
  .mobile-card-image-placeholder .pi {
    font-size: 1.5rem;
    color: #92a0b7;
  }
  .mobile-card-body {
    flex: 1;
    min-width: 0;
  }
  .mobile-card-top {
    display: flex;
    align-items: flex-start;
    justify-content: space-between;
    gap: 0.5rem;
    margin-bottom: 0.4rem;
  }
  .mobile-card-main {
    flex: 1;
    min-width: 0;
  }
  .mobile-code {
    margin-bottom: 0.1rem;
    font-size: 0.72rem;
    line-height: 1.2;
    color: #8b98b0;
    letter-spacing: 0.02em;
    font-weight: 700;
  }
  .mobile-name {
    font-size: 1rem;
    line-height: 1.25;
    font-weight: 700;
    color: var(--mobile-text);
    word-break: break-word;
  }
  .mobile-card-footer {
    display: flex;
    align-items: center;
    justify-content: space-between;
    gap: 0.5rem;
    flex-wrap: wrap;
  }
  .mobile-card-summary {
    font-size: 0.8rem;
    color: var(--mobile-muted);
    font-weight: 600;
  }
  .mobile-card-actions {
    display: flex;
    align-items: center;
    gap: 0.35rem;
  }
  .mobile-eye-action {
    width: 2.05rem;
    height: 2.05rem;
    color: var(--mobile-primary) !important;
  }
  .mobile-state {
    margin-top: 1rem;
    background: #f8faff;
    border: 1px dashed var(--mobile-border);
    border-radius: 1rem;
    padding: 1rem;
    text-align: center;
    color: var(--mobile-muted);
    font-size: 0.9rem;
  }
  .mobile-load-more {
    padding-top: 0.4rem;
  }
  .mobile-fab {
    position: fixed;
    bottom: calc(1rem + env(safe-area-inset-bottom));
    right: calc(1rem + env(safe-area-inset-right));
    z-index: 25;
    width: 3.5rem;
    height: 3.5rem;
    min-width: 3.5rem;
    min-height: 3.5rem;
    padding: 0;
    border: none;
    border-radius: 50%;
    background: var(--mobile-primary);
    color: #fff;
    box-shadow: 0 4px 14px rgba(95, 107, 255, 0.45);
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 0.35rem;
    font-weight: 700;
    font-size: 0.9rem;
    cursor: pointer;
    transition: transform 0.2s ease, box-shadow 0.2s ease;
    -webkit-tap-highlight-color: transparent;
  }
  .mobile-fab:active {
    transform: scale(0.96);
  }
  .mobile-fab .pi {
    font-size: 1.35rem;
  }
  .mobile-fab-label {
    display: none;
  }
}
@media screen and (max-width: 768px) and (min-width: 400px) {
  .mobile-fab {
    width: auto;
    height: 3rem;
    min-height: 3rem;
    padding: 0 1.25rem;
    border-radius: 999px;
  }
  .mobile-fab-label {
    display: inline;
  }
}
@media screen and (max-width: 768px) {
  :host ::ng-deep .mobile-action-btn {
    width: 100%;
    justify-content: flex-start;
    border: 0 !important;
    background: transparent !important;
    color: #4f5d79 !important;
    padding: 0.35rem 0.45rem !important;
    font-weight: 700;
    border-radius: 0.7rem;
    box-shadow: none !important;
  }
  :host ::ng-deep .mobile-action-btn .p-button-icon {
    color: var(--mobile-primary);
    font-size: 0.95rem;
  }
  :host ::ng-deep .mobile-action-btn .p-button-label {
    font-size: 0.95rem;
  }
  :host ::ng-deep .mobile-status-tag {
    font-size: 0.79rem;
    font-weight: 700;
    border-radius: 0.55rem;
    padding: 0.22rem 0.55rem;
  }
  :host ::ng-deep .mobile-status-tag.p-tag-success {
    background: #d9f7e5;
    color: #168f4a;
  }
  :host ::ng-deep .mobile-status-tag.p-tag-warn,
  :host ::ng-deep .mobile-status-tag.p-tag-warning {
    background: #ffe8d3;
    color: #dc6408;
  }
  :host ::ng-deep .mobile-status-tag.p-tag-danger {
    background: #ffe3e1;
    color: #dd3f39;
  }
  :host ::ng-deep .mobile-status-tag.p-tag-secondary {
    background: #e8edf6;
    color: #64748b;
  }
  :host ::ng-deep .mobile-delete-action {
    width: 2.05rem;
    height: 2.05rem;
    border-radius: 999px !important;
    border-color: #ffb9b4 !important;
    color: #ef4444 !important;
    background: rgba(255, 255, 255, 0.9) !important;
  }
  :host ::ng-deep .mobile-delete-action .p-button-icon {
    font-size: 0.84rem;
  }
  :host ::ng-deep .mobile-load-more-btn {
    width: 100%;
    border-radius: 999px;
    justify-content: center;
    font-weight: 700;
    border: 1px solid #cfd7ff;
    background: #eef2ff;
    color: #4f5df4;
  }
}
@keyframes mobileFade {
  from {
    opacity: 0;
    transform: translateY(8px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}
